@use "variables" as v;
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: v.$main-font;
  font-size: 1.6rem;
  &.fijar-body {
    overflow: hidden;
  }
  &.body-scroll {
    padding-top: 86px;
  }
}

p {
  font-size: 2rem;
  color: v.$black;
  line-height: 3rem;
}

.contenedor {
  width: 100%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}
h2 {
  font-size: 3.6 rem;
}
h3 {
  font-size: 3rem;
}
img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}
